import { z } from 'zod';
const ProblemModel = z.object({
    name: z.string(),
    problem: z.string(),
    points: z.string(),
});

export const createContestSchema = z.object({
    nameContest: z
        .string()
        .min(2, '      2 '),
    participantCount: z
        .string()
        .min(1, '      1'),
    judgeCount: z.string().min(1, '      1'),
    prefix: z
        .string()
        .min(2, '      2 '),
    duration: z
        .string()
        .regex(
            /^\d{2}:\d{2}$/,
            '     (00:00).',
        ),
    //problemInfos: z.array(ProblemModel),
});
