import { z } from 'zod';

export const judgeFeedbackSchema = z.object({
    accepted: z.enum(['accept', 'not-accept'], { message: ' ' }),
    points: z
        .string()
        .min(0, { message: 'dasdas' })
        .refine(
            (value) => {
                if (value) {
                    return /^\d+$/.test(value);
                } else {
                    return false;
                }
            },
            {
                message: ' ', //   ,      
            },
        ),
    comment: z.string().nullable(),
});
